import styled from "styled-components";

const labelColor = () => {
  return "12px";
};

const Label = styled.label`
  background: ${props => {
    if (props.type === "api") {
      return "#67C867";
    } else if (props.type === "video") {
      return "#FF9B48";
    } else if (props.type === "form") {
      return "#AF957F";
    } else {
      return "black";
    }
  }};
  font-size: 12px;
  margin-right: 5px;
  margin-top: 5px;
  border-radius: 5px;
  min-width: 55px;
  text-align: center;
  padding: 6px 0;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
`;

export default props => {
  return (
    <Label type={props.type} className="label-tag">
      {props.name}
    </Label>
  );
};
